import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EVBatteryManufacturersCrudComponent } from './EV-battery-manufacturers-crud/EV-battery-manufacturers-crud.component';
import { TABS_DEMO_ROUTES } from './EV-battery-manufacturers-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes: Routes = [
    {
        path: 'crud', component: EVBatteryManufacturersCrudComponent, canActivate: [AuthGuard], children: TABS_DEMO_ROUTES
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class EVBatteryManufacturersRoutingModule { }